.page-footer {
  height: 230px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 29px 0;
  justify-content: space-between;
}

.page-footer__hash-tag {
  @include font-l;
  text-transform: uppercase;
  font-weight: bold;
}

.page-footer__dev-info {
  display: flex;
  align-items: center;
}
